import React, { FunctionComponent } from 'react'
import styled from '@emotion/styled'
import NavRight from './NavRight'
import NavLeft from './NavLeft'

const MainNavContainer = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 110px;
  background-color: #fff;
  border-bottom: solid 1px #ebe8e8;
`
const MainNavWrapA = styled.div`
  width: 100%;
  height: 100%;
  margin: 0 auto;

  @media (min-width: 1200px) {
    max-width: 1200px;
    display: flex;
    align-items: center;
  }
`

const MainNav: FunctionComponent = function () {
  return (
    <MainNavContainer>
      <MainNavWrapA>
        <NavLeft />
        <NavRight />
      </MainNavWrapA>
    </MainNavContainer>
  )
}

export default MainNav
